@import "src/common/style/variables";
.header {
  padding: 12px 0;
  background: #FCFBFB;
  box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
  border-radius: 0px;
  position: relative;
  z-index: 1;
}

.title {
  font-weight: 600;
  font-size: 26px;
  line-height: 36px;
  color:$text_primary_color;
  padding-left: 64px;
}